<template>
   <section id="login" class="margin">
        <span class="jump">
              
          </span>
        <div class="cont clear">
            <div class="login">
                <i>本来会员登录</i>
                <p><label>邮箱/手机</label><input type="text" class="user" v-model="username"></p>
                <p><label>密码</label><input type="password" class="pass" v-model="password"></p>
                <li to="/user" class="btn" @click="login">立即登录</li><br>
                <span><router-link to="/reg">注册</router-link><b>还不是会员？</b></span>
            </div>
            <img src="https://ecimg.happigo.com/data/upload/shop/store/goods/1/38/267038/1_main_267038_360.jpg" class="cont-r" alt="">
        </div>
        <div class="mask"></div>
    </section>
</template>
<script>
export default {
  data(){
    return {
      username:'',
      password:'',
      mess:''
    }
  },
  methods:{
    login(){
      axios({
        url:'/api/login',
        method:'post',
        data:{username:this.username,password:this.password}
      }).then(
        res => {
          // console.log('1',res);
          if(res.data.err===0){
            this.$router.push('/user')
          }else{
            this.mess = res.data.msg
          }
        }
      )
    }
  }
}
</script>

